footer {
    align-items: center;
    box-shadow: 1px 1px 10px #fac0ca;
    display: flex;
    justify-content: space-evenly;
    margin-top: 4%;
    padding: 5px;
    width: 100%;
}

.footer a {
    text-decoration: none;
}
.footer p {
    color: white;
    margin-top: 4%;
}

